html, body {
    overflow: hidden;
}

.App {
    text-align: center;
    display: flex;
    justify-content: space-around;
    align-items: center;
    height: 100vh;
    padding: 0 40px 0 25px;
    gap: 25px;
}

* {
    box-sizing: content-box;
}


/* Scroll 1 */

/* Scroll 4 */
*::-webkit-scrollbar {
    width: 9px;
    height: 9px;
}

*::-webkit-scrollbar-track {
    border-radius: 10px;
    background-color: #ececec;
}

*::-webkit-scrollbar-thumb {
    background-color: rgba(0, 0, 0, 0.2);
}

.vector-container {
    --width: 700px;
    width: var(--width);
    height: var(--width);
    position: relative;
}

.vector-container.move__mode svg {
    cursor: move;
}

.vector-container.color_fill__mode svg {
    cursor: url("./assets/images/Fill.svg"), auto;
}


.vector-y-axios, .vector-x-axios {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background: black;
    pointer-events: none;
    z-index: -1;
}


.vector-y-axios {
    height: var(--width);
    width: 2px;
    pointer-events: none;
}

.vector-x-axios {
    height: 2px;
    width: var(--width);
    pointer-events: none;
}

.vector-y-arrow {
    pointer-events: none;
    position: absolute;
    left: 50%;
    top: 0;
    transform: translate(-50%, -11px);
}

.vector-x-arrow {
    pointer-events: none;
    position: absolute;
    right: 0;
    top: 50%;
    transform: translate(10px, -50%) rotate(90deg);
}

.vector-y-max {
    position: absolute;
    left: 50%;
    top: 0;
    user-select: none;
    transform: translate(calc(-50% + 30px), -20px);
}

.vector-x-max {
    position: absolute;
    right: 0;
    top: 50%;
    user-select: none;
    transform: translate(35px, -50%) rotate(90deg);
}

.dot {
    position: absolute;
    width: 2px;
    height: 2px;
    border-radius: 50%;
    background: #665908;
    pointer-events: none;
}

.point {
    position: absolute;
    transform: translate(-50%, -50%);
}

.svg {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    pointer-events: none;
}

.svg * {
    pointer-events: auto;
}

.svg.active {
    z-index: 99999 !important;
}


.svg .circle {
    display: none;
}

.preserve-points .svg .circle {
    display: block;
}

.svg.active .circle {
    display: block;
    fill: red !important;
}

.circle {
    cursor: pointer;
}

.celebration {
    position: absolute;
    top: -100px;
    left: 100px;
}

.fade-enter {
    opacity: 0.01
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: opacity 300ms ease-in
}

.fade-exit {
    opacity: 1
}

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: opacity 300ms ease-in
}
